import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Line } from 'react-chartjs-2';
import './style.css';
import { fetchData } from '../../actions';
import axios from 'axios';

const DynamicChart = props => {
  const [chartData, setChartData] = useState({});
  const [covidData, setCovidData] = useState({});
  // const covidData = props.deathData;

  // const deathsObj = covidData.timeline.deaths;
  // const deathArray = Object.values(deathsObj);
  // const deathDates = Object.keys(deathsObj);
  // const dailyDeathArray = [];
  // for (let i = 1; i < 16; i++) {
  //   let dailyDeathCount = deathArray[i] - deathArray[i - 1];
  //   dailyDeathArray.push(dailyDeathCount);
  // }

  const fetchData = async () => {
    await axios
      .get('https://disease.sh/v3/covid-19/historical/usa?lastdays=16')
      .then(response => {
        // console.log(response.data.timeline);
        setCovidData(response.data.timeline.deaths);
        const deathsObj = covidData;
        const deathArray = Object.values(deathsObj);
        const deathDates = Object.keys(deathsObj);
        const dailyDeathArray = [];
        for (let i = 1; i < 16; i++) {
          let dailyDeathCount = deathArray[i] - deathArray[i - 1];
          dailyDeathArray.push(dailyDeathCount);
        }
        Chart(deathDates, dailyDeathArray);
      })
      .catch(error => {
        throw error;
      });
  };
  const Chart = (deathDates, dailyDeathArray) => {
    setChartData({
      labels: deathDates,
      // labels: ['January', 'February', 'March', 'April'],
      datasets: [
        {
          label: 'Deaths',
          data: dailyDeathArray,
          // data: [400, 321, 109, 527],
          borderColor: 'red',
          backgroundColor: 'red',
          borderWidth: 1.5,
        },
      ],
    });
  };
  useEffect(() => {
    fetchData();
  }, []);
  return (
    <div className='container'>
      <div className='chartContainer'>
        <Line
          data={chartData}
          options={{
            responsive: true,
            title: { text: 'THICCNESS SCALE', display: true },
            scales: {
              y: {
                grace: '0.5%',
                beginAtZero: false,
              },
            },
          }}
        />
      </div>
    </div>
  );
};

export default DynamicChart;
